.main {
  float: right;
  min-height: 100vh;
  padding: 70px 0 50px;
  width: calc(100% - 230px);
  background-color: #f5f5f5;
  font-family: $mainFont;
  @include md {
    width: calc(100% - 180px); }
  @include sm {
    float: none;
    width: 100%; }
  &__header {
    padding-bottom: 24px;
    border-bottom: 2px solid rgba(#bcc7d3, 0.4);
    margin-bottom: 40px;
    margin-top: 50px;
    h2 {
      color: #78909c;
      font-size: 14px;
      font-weight: 400;
      letter-spacing: 0.015em;
      line-height: 1.5;
      .name {
        color: #37474f;
        font-size: 18px; } } }
  &__inner {
    padding: 0 72px;
    @include xl {
      padding: 0 30px; }
    @include md {
      padding: 0 15px; } }
  &__top-chart {
      padding: 20px 20px 10px;
      background-color: $white;
      margin-bottom: 40px;
      @include sm {
        padding: 20px 0 10px; } }
  &__bottom-charts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 20vh;
    min-height: 192px;
    @include xl {
      min-height: 150px; } } }

.area-chart-controls {
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between; }

.area-chart-wrapper {
  position: relative;
  height: 30vh;
  min-height: 300px; }

.donat-chart-wrapper {
  background-color: $white;
  padding: 18px;
  @include fsize(30%);
  position: relative;
  @include xl {
    padding: 5px; }
  @include sm {
    @include fsize(100%);
    padding: 5px;
    margin-bottom: 12px; }
  // canvas
 }  //   width: 100%

.bar-chart-wrapper {
  background-color: $white;
  padding: 15px;
  @include fsize(calc(70% - 12px));
  position: relative;
  @include xl {
    padding: 5px; }
  @include sm {
    @include fsize(100%);
    padding: 5px; }
  // canvas
 }  //   width: 100%
