.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background-color: #e0e0e0;
  display: flex;
  z-index: 100;
  @include sm {
    height: 50px; }
  &__title {
    background-color: $darkBlue;
    @include fcenter;
    @include fsize(230px);
    @include md {
      @include fsize(180px); } } }
