body {
  background: #FFF;
  line-height: 1.4;
}

.block {
  display: flex;
}

.sidebar {
  position: fixed;
  top: 70px;
  left: 0;
  bottom: 0;
  width: 230px;
  background-color: #37474f;
  font-family: "Open Sans", sans-serif;
}

@media only screen and (max-width: 1023px) {
  .sidebar {
    width: 180px;
  }
}

@media only screen and (max-width: 767px) {
  .sidebar {
    width: 100%;
    top: 50px;
    bottom: auto;
  }
}

@media only screen and (max-width: 767px) {
  .sidebar__menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    transform: translateY(101%);
    transition: transform 0.4s;
  }
}

.sidebar-menu__item {
  display: flex;
  align-items: center;
  padding: 20px 22px;
  opacity: 0.5;
  transition: background-color 0.4s, opacity 0.4s;
}

@media only screen and (max-width: 1023px) {
  .sidebar-menu__item {
    padding: 16px 8px;
  }
}

.no-touch .sidebar-menu__item:hover {
  background-color: #455a64;
  opacity: 1;
}

.sidebar-menu__link {
  text-decoration: none;
  color: #fff;
}

.sidebar-menu__icon {
  color: #fff;
  margin-right: 16px;
  font-size: 0;
  display: inline-block;
  vertical-align: middle;
}

@media only screen and (max-width: 1023px) {
  .sidebar-menu__icon {
    margin-right: 8px;
  }
}

.sidebar-menu__icon .icon {
  font-size: 16px;
  max-width: 16px;
}

.sidebar-menu__text {
  font-size: 14px;
  display: inline-block;
  vertical-align: middle;
}

@media only screen and (max-width: 1023px) {
  .sidebar-menu__text {
    font-size: 13px;
  }
}

.main {
  float: right;
  min-height: 100vh;
  padding: 70px 0 50px;
  width: calc(100% - 230px);
  background-color: #f5f5f5;
  font-family: "AvenirNextLTPro", sans-serif;
}

@media only screen and (max-width: 1023px) {
  .main {
    width: calc(100% - 180px);
  }
}

@media only screen and (max-width: 767px) {
  .main {
    float: none;
    width: 100%;
  }
}

.main__header {
  padding-bottom: 24px;
  border-bottom: 2px solid rgba(188, 199, 211, 0.4);
  margin-bottom: 40px;
  margin-top: 50px;
}

.main__header h2 {
  color: #78909c;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.015em;
  line-height: 1.5;
}

.main__header h2 .name {
  color: #37474f;
  font-size: 18px;
}

.main__inner {
  padding: 0 72px;
}

@media only screen and (max-width: 1365px) {
  .main__inner {
    padding: 0 30px;
  }
}

@media only screen and (max-width: 1023px) {
  .main__inner {
    padding: 0 15px;
  }
}

.main__top-chart {
  padding: 20px 20px 10px;
  background-color: #fff;
  margin-bottom: 40px;
}

@media only screen and (max-width: 767px) {
  .main__top-chart {
    padding: 20px 0 10px;
  }
}

.main__bottom-charts {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 20vh;
  min-height: 192px;
}

@media only screen and (max-width: 1365px) {
  .main__bottom-charts {
    min-height: 150px;
  }
}

.area-chart-controls {
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
}

.area-chart-wrapper {
  position: relative;
  height: 30vh;
  min-height: 300px;
}

.donat-chart-wrapper {
  background-color: #fff;
  padding: 18px;
  flex: 0 0 30%;
  min-width: 30%;
  position: relative;
}

@media only screen and (max-width: 1365px) {
  .donat-chart-wrapper {
    padding: 5px;
  }
}

@media only screen and (max-width: 767px) {
  .donat-chart-wrapper {
    flex: 0 0 100%;
    min-width: 100%;
    padding: 5px;
    margin-bottom: 12px;
  }
}

.bar-chart-wrapper {
  background-color: #fff;
  padding: 15px;
  flex: 0 0 calc(70% - 12px);
  min-width: calc(70% - 12px);
  position: relative;
}

@media only screen and (max-width: 1365px) {
  .bar-chart-wrapper {
    padding: 5px;
  }
}

@media only screen and (max-width: 767px) {
  .bar-chart-wrapper {
    flex: 0 0 100%;
    min-width: 100%;
    padding: 5px;
  }
}

.ss-main .ss-multi-selected,
.ss-main .ss-single-selected {
  border: none;
}

.ss-content {
  border-radius: 10px;
  overflow: hidden;
}

.ss-option {
  white-space: nowrap;
  padding: 10px 20px;
}

.ss-main .ss-single-selected .placeholder {
  padding-right: 8px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background-color: #e0e0e0;
  display: flex;
  z-index: 100;
}

@media only screen and (max-width: 767px) {
  .header {
    height: 50px;
  }
}

.header__title {
  background-color: #1e88e5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 230px;
  min-width: 230px;
}

@media only screen and (max-width: 1023px) {
  .header__title {
    flex: 0 0 180px;
    min-width: 180px;
  }
}
