//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$avenir: 'AvenirNextLTPro';
$openSans: 'Open Sans';

@font-face {
	@include fontdefault(normal, 400, $avenir);
	@include fontpath('AvenirNextLTPro-Regular', 'AvenirNextLTPro-Regular', AvenirNextLTPro-Regular); }

@font-face {
	@include fontdefault(normal, 400, $openSans);
	@include fontpath('OpenSans-Regular', 'OpenSans-Regular', OpenSans-Regular); }

@font-face {
	@include fontdefault(normal, 700, $openSans);
	@include fontpath('OpenSans-Bold', 'OpenSans-Bold', OpenSans-Bold); }
